import React from 'react';
import { bool, node } from 'prop-types';
import { compose } from 'redux';
import { withStyles } from 'components/WithStyle';
import s from './Layout.css';
import Header from '../Header';
import Footer from '../Footer';
import StrictModeCheck from '../StrictMode';

class Layout extends React.Component {
  static propTypes = {
    children: node.isRequired,
    fullHeight: bool,
  };

  static defaultProps = {
    fullHeight: false,
  };

  render() {
    return (
      <div
        className={this.props.fullHeight ? s.fullHeight : ''}
        style={{ height: '100%' }}
      >
        <Header hiddenXs />
        {this.props.children}
        <Footer hiddenXs />
      </div>
    );
  }
}

export { Layout as LayoutComponent };
export default compose(
  withStyles(s),
  StrictModeCheck,
)(Layout);
