import React, { Component } from 'react';
import { defineMessages, FormattedMessage } from 'react-intl';
import { func } from 'prop-types';
import { withStyles } from 'components/WithStyle';
import s from './Navigation.css';
import Link from '../Link';

const messages = defineMessages({
  about: {
    id: 'navigation.about',
    defaultMessage: 'About',
    description: 'About link in header',
  },
  contact: {
    id: 'navigation.contact',
    defaultMessage: 'Contact',
    description: 'Contact link in header',
  },
  login: {
    id: 'navigation.login',
    defaultMessage: 'Log in',
    description: 'Log in link in header',
  },
  or: {
    id: 'navigation.separator.or',
    defaultMessage: 'or',
    description: 'Last separator in list, lowercase "or"',
  },
  signup: {
    id: 'navigation.signup',
    defaultMessage: 'Sign up',
    description: 'Sign up link in header',
  },
});

class Navigation extends Component {
  static propTypes = {
    closeMobileMenu: func,
  };

  static defaultProps = {
    closeMobileMenu: null,
  };

  render() {
    const { closeMobileMenu } = this.props;
    return (
      <ul className={s.root}>
        <li>
          <Link onClick={closeMobileMenu} className={s.link} to="/about">
            <FormattedMessage {...messages.about} />
          </Link>
        </li>
        <li>
          <Link onClick={closeMobileMenu} className={s.link} to="/contact">
            <FormattedMessage {...messages.contact} />
          </Link>
        </li>
      </ul>
    );
  }
}

export default withStyles(s)(Navigation);
