import React from 'react';
import PropTypes from 'prop-types';
import { defineMessages, FormattedMessage } from 'react-intl';
import { withStyles } from 'components/WithStyle';
import cx from 'classnames';
import s from './Footer.css';

const messages = defineMessages({
  copyright: {
    id: 'footer.copyright',
    defaultMessage: '© 2018 Bản quyền thuộc về Home Credit',
    description: 'Copyright text link in footer',
  },
});

class Footer extends React.PureComponent {
  static propTypes = {
    hiddenXs: PropTypes.bool,
  };
  static defaultProps = {
    hiddenXs: false,
  };

  render() {
    const rootClasses = cx(s.root, {
      [s.hiddenXs]: this.props.hiddenXs,
    });
    return (
      <div className={rootClasses}>
        <div className={s.container}>
          <span className={s.text}>
            <FormattedMessage {...messages.copyright} />
          </span>
        </div>
      </div>
    );
  }
}

export default withStyles(s)(Footer);
