import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'components/WithStyle';
import cx from 'classnames';
import s from './Header.css';
import Link from '../Link';
import logoUrl from './logo.png';
import HamburgerButton from '../HamburgerButton';
import Navigation from '../Navigation';

class Header extends React.Component {
  static propTypes = {
    hiddenXs: PropTypes.bool,
  };

  static defaultProps = {
    hiddenXs: false,
  };

  constructor() {
    super();
    this.state = {
      mobileMenuClosed: true,
    };

    this.mobileMenuContainer = React.createRef();
  }

  componentDidMount() {
    window.addEventListener('click', this.detectClickOutsideMobileMenu);
  }

  componentWillUnmount() {
    window.removeEventListener('click', this.detectClickOutsideMobileMenu);
  }

  detectClickOutsideMobileMenu = e => {
    const mobileMenuContainer = this.mobileMenuContainer.current;

    if (mobileMenuContainer.contains(e.target)) {
      return;
    }

    this.closeMobileMenu();
  };

  closeMobileMenu = () => {
    this.setState({ mobileMenuClosed: true });
  };

  handleHamburgerBtnClick = () => {
    this.setState({ mobileMenuClosed: !this.state.mobileMenuClosed });
  };

  render() {
    const { mobileMenuClosed } = this.state;
    const rootClasses = cx(s.root, {
      [s.hiddenXs]: this.props.hiddenXs,
    });

    return (
      <div className={rootClasses}>
        <div className={s.logo}>
          <Link to="/">
            <img src={logoUrl} height="27" alt="Homecredit" />
          </Link>
        </div>
        <div className={s.largeScreenNav}>
          <Navigation />
        </div>
        <div ref={this.mobileMenuContainer} className={s.mobileNav}>
          <HamburgerButton
            {...this.state}
            onClick={this.handleHamburgerBtnClick}
          />
          {!mobileMenuClosed && (
            <Navigation closeMobileMenu={this.closeMobileMenu} />
          )}
        </div>
      </div>
    );
  }
}

export default withStyles(s)(Header);
