import React, { Component } from 'react';
import { oneOfType, func, node, bool, string, arrayOf } from 'prop-types';
import { withStyles } from 'components/WithStyle';
import HeaderBar from 'components/HeaderBar';
import classNames from 'classnames';
import style from './PzLayout.css';

class PzLayout extends Component {
  static propTypes = {
    children: oneOfType([arrayOf(node), node]).isRequired,
    onBackClick: func,
    onNextClick: func,
    hideNextButton: bool,
    hideBackButton: bool,
    nextText: string,
    className: string,
  };

  static defaultProps = {
    onBackClick: () => {},
    onNextClick: () => {},
    hideNextButton: false,
    hideBackButton: false,
    className: '',
    nextText: '',
  };

  render() {
    const {
      hideBackButton,
      hideNextButton,
      onBackClick,
      onNextClick,
      className,
      nextText,
    } = this.props;
    return (
      <div className={classNames(className)}>
        <div className={style.layoutHeader}>
          <HeaderBar
            title="Competition Report"
            onBackClick={onBackClick}
            onNextClick={onNextClick}
            hideNextButton={hideNextButton}
            hideBackButton={hideBackButton}
            nextText={nextText}
          />
        </div>
        <div className={style.layoutBody}>{this.props.children}</div>
      </div>
    );
  }
}

export default withStyles(style)(PzLayout);
